import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	installApp = (e) => {
		console.log(e)
		
		connect.send("VKWebAppAddToCommunity", {})
		
		setInterval(() => {
			if(e.detail !== undefined){
				this.setState({ activePanel: 'persik' })
				console.log(e)
				// console.log('тут после установки', e.detail.data.group_id)
			}
		}, 1000);
			
		
		// () => {
		// 	if(e.detail.data.group_id === true){
		// 		this.setState({ activePanel: 'persik' })
		// 	}
		// }
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} installApp={this.installApp} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
